  .container {
    max-width: 100vw;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 35px;
    margin: 0 auto;
    padding: 40px 0;
  }
    .cards {
      position: relative;
      width: 300px;
      height: 400px;
      margin: 0 auto;
      background: #000;
      border-radius: 15px;
      box-shadow: 0 15px 60px rgba(0, 0, 0, 0.5);
    }
      .face {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
        .face1 {
          box-sizing: border-box;
          padding: 20px;}

          h2 {
            margin: 0;
            padding: 0;
          }

          .java {
            background-color: #fffc00;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }

          .python {
            background-color: #00fffc;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }

          .cSharp {
            background-color: #fc00ff;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        

        .face2 {
          transition: 0.5s;}

          h2 {
            margin: 0;
            padding: 0;
            font-size: 10em;
            color: #fff;
            transition: 0.5s;
            text-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
            z-index: 10;
          }
        
      
    

    .cards:hover .face.face2 {
      height: 60px;}

      h2 {
        font-size: 2em;
      }
    

    .cards:nth-child(1) .face.face2 {
      background-image: linear-gradient(
        40deg,
        #bbba81 0%,
        #d5a4d6 45%,
        #759d9c 100%
      );
      border-radius: 15px;
    }

    .cards:nth-child(2) .face.face2 {
      background-image: linear-gradient(
        40deg,
        #bbba81 0%,
        #d5a4d6 45%,
        #759d9c 100%
      );
      border-radius: 15px;
    }

    .cards:nth-child(3) .face.face2 {
      background-image: linear-gradient(
        40deg,
        #bbba81 0%,
        #d5a4d6 45%,
        #759d9c 100%
      );
      border-radius: 15px;
    }
  
